@media (max-width: 767px) {
  .header {
    height: 420px;

     #carousel {
      width: 500px;
    }

    .bottom-header {
      top: 215px;
    }

    .bx-wrapper .bx-viewport {
      height: 148px !important;
    }
  }


  .content {
    &:before {
      top: -30px;
    }
  }

  .home {
    h2 {
      padding-top: 10px;
    }

    .home-values {
      .description {
        padding: 10px 0;
        height: auto;
        line-height: 25px;
        font-size: 18px;
      }
    }
  }

  .contact {
    h2 {
      padding-top: 10px;
    }

    .bottom-form {
      .left-column, .right-column {
        float: none;
        width: 100%
      }
      .left-column {
        margin-bottom: 20px;
      }
      .right-column{
        text-align: center;
      }
    }

      .map {
        height: 324px;
        iframe {
          height: 320px;
        }
      }
  }

  .bottom-address {
    width: 90%;
  }

  .footer {
    .container {
      width: 90%;
    }

    .copyright {
      text-align: center;
      .dot {
        display: none;
      }
      .reserved {
        display: block;
        margin-top: 5px;
      }
    }
  }

  .thanks-content {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;

    .wrapper {
      display: block;
      vertical-align: inherit;
      margin-top: 30px;
      width: 90%;
    }    

    .message {
      height: auto;
      margin: inherit;
      padding: 30px 0;
      width: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;

      h2 {
        font-size: 1.2em;
      }
    }
  }

  .back-to-top {
    right: 10px;
    bottom: 60px;
  }
}